.card {
  display: flex;
  flex-direction: column;

  max-width: 198px;
  padding: 32px 16px 16px;

  border: 1px solid #{$color-for-border};
  border-radius: 5px;

  &__image {
    align-self: center;
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__title {
    font-size: 12px;
    font-weight: 500;

    width: 166px;
    line-height: 18px;
    margin-bottom: 4px;

    color: #{$color-main};
  }

  &__product-code {
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;

    color: #{$color-secondary};
    margin-bottom: 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;

    margin-bottom: 24px;
  }

  &__reviews {
    font-weight: 400;
    font-size: 10px;
    line-height: 16px;

    color: #{$color-main};
  }

  &__price {
    display: flex;
    justify-content: space-between;

    margin-bottom: 16px;
    color: #{$color-secondary};
  }

  &__price-text {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }

  &__price-value {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;

    color: #{$color-main};
  }

  &__button {
    box-sizing: border-box;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;

    height: 40px;
    border-radius: 5px;
    border: 1px solid #{$color-blue};
    background-color: #{$color-blue};

    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-decoration: none;

    color: #{$color-white};
    transition: color 0.3s ease, background-color 0.3s ease;

    &:hover {
      background-color: #{$color-white};
      color: #{$color-blue};
    }
  }
}
